.sideNav {
    position: sticky;
    top: 0;
    width: 200px;
}

.sideNav>ul li {
    padding: 25px 20px;
    transition: all .2s;
    color: white;
}
.sideNav>ul .active {
    background-color: #018273;
}
.sideNav>ul li:hover {
    padding: 25px;
    background-color: #01827356;
}
.sideNav>ul .active:hover {
    padding: 25px;
    background-color: #018273;
}
a:-webkit-any-link {
    text-decoration: none;
}


