.question-container {
    margin: 20px;
    padding: 25px;
    background-color: #fff;
    border-radius: 3px;
}
.question {
    display: flex;
    justify-content: space-between;
}

.answer-container {
    padding: 30px 0;
}
.answer {
    cursor: pointer;
    padding: 10px 20px;
    margin-right: 10px;
    border-radius: 3px;
}